import UploadFileOutlined from "@mui/icons-material/CloudUploadOutlined";
import FolderCopyOutlined from "@mui/icons-material/FolderCopyOutlined";
import { Box, Card } from "@mui/material";
import { useRef } from "react";
import { useSearchParams } from "react-router-dom";
import { useMiddleware } from "../../../../Application";
import ResponsiveActionBar from "../../../../components/ResponsiveActionBar";
import { TabView } from "../../../../tabs";
import useResponsiveTableFetch from "../../../../utils/useResponsiveTableFetch";
import FilesTable from "../../components/FilesTable";
import { useSnackbar } from "notistack";


const Main = () => {

    const uploadEl = useRef();

    const middleware = useMiddleware('Storage.Main');

    const [searchParams] = useSearchParams();

    const { data, total, loading, loadMore, reload } = useResponsiveTableFetch({
        handleFetch: params => middleware.list({
            ...params,
            ...Object.fromEntries([...searchParams])
        }),
        loadDeps: [searchParams],
        infiniteScrollDefaultParams: {
            page: searchParams.has('page') ? searchParams.get('page') : 0,
            perPage: searchParams.has('perPage') ? searchParams.get('perPage') : 25,
        }
    });

    const { enqueueSnackbar } = useSnackbar();

    const uploadFiles = e => {
        let filesToUpload = [];
        for (let i = 0; i < e.target.files.length; i++) {
            filesToUpload = [...filesToUpload, {
                file: e.target.files[i]
            }];
        }

        enqueueSnackbar('Uploading some file', {
            variant: "uploadFile",
            persist: true,
            files: filesToUpload,
            onComplete: reload
        });
    }


    return (
        <TabView title="مدیریت فایل‌ها" Icon={FolderCopyOutlined}>
            <Box sx={{ height: { xs: '100%', md: 'auto' }, display: 'flex', flexDirection: 'column', p: { xs: 0, sm: 2, md: 3 } }}>
                <Box
                    sx={{
                        display: "flex",
                        mb: { xs: 0, md: 1 }
                    }}
                >
                    <Box sx={{ flexGrow: 1 }} />
                    <ResponsiveActionBar
                        actions={[
                            {
                                title: 'بارگذاری فایل',
                                icon: <UploadFileOutlined />,
                                onClick: () => uploadEl.current && uploadEl.current.click()
                                // onClick: () => setUploadDialogProps({ open: true })
                            },
                        ]}
                    />
                </Box>
                <Card variant="outlined" sx={{ flexGrow: 1 }}>
                    <FilesTable
                        files={data}
                        loading={loading}
                        meta={{ total }}
                        infiniteScrollProps={{
                            loading: false,
                            hasNextPage: data.length < total,
                            onLoadMore: loadMore
                        }}
                    />
                </Card>
            </Box>

            <input
                type="file"
                multiple
                ref={uploadEl}
                style={{ display: 'none' }}
                onChange={uploadFiles}
            />
        </TabView>
    )
}

export default Main;